import { modalPages } from "./language";
import { $ } from "./utils";

class Modal {
    _open = false;
    _closeBtn = $('.close-btn');
    _puzzle;

    constructor() {
        window.addEventListener('click', (e) => {
            if (e.target === $('.modal-overlay')) {
                this.hide();
            }
        });
        window.addEventListener('keydown', event => {
            if (event.key == 'Escape') {
                this.hide();
            } else {
                if (this._open)
                    event.stopImmediatePropagation();
            }
        }); 
        modalPages.forEach(page => {
            $(`.${page}-btn`).addEventListener('click', e => this.show(page));
        });

        this._closeBtn.addEventListener('click', () => this.hide());
    }

    get puzzle() {
        return this._puzzle;
    }

    set puzzle(p) {
        this._puzzle = p;
    }

    toggle() {
        this._open = !this._open;
        if (this._open) {
            $('.modal-overlay').style.display = 'flex';
            setTimeout(() => {
                $('.modal').classList.add('open');                 
            }, 10);
        } else {
            $('.modal').classList.remove('open');
            setTimeout(() => {
                $('.modal-overlay').style.display = 'none';
            }, 300);
        }
    }

    show(msg = 'help') {

        if (!this._open) {            
            //Calculate & display statistics
            if (msg === 'stats' && this.puzzle) {
                document.querySelector('.played.score').innerHTML = this.puzzle.stats.played;
                document.querySelector('.won.score').innerHTML = this.puzzle.wonCount;
                const scoreBins = document.querySelectorAll('.guess-dist>.score-bar');
                scoreBins.forEach((bin, i) => {
                    const count = this.puzzle.stats.dist[i] || 0;
                    const width = count === 0 ? 'fit-content' : count / this.puzzle.maxWin * 100 + '%';
                    bin.style.width = width;
                    bin.innerHTML = count;                    
                });
            }
            modalPages.map(pname => $(`.${pname}`)).forEach(pane => {
                if (pane.id === msg) {
                    pane.style.display = 'flex';
                } else {
                    pane.style.display = 'none';
                }
            })
            this.toggle();
        }
    }
    hide() {
        if (this._open) this.toggle();
    }
    
    showError(error) {
        const errorDiv = document.querySelector('.error-msg');
        errorDiv.innerHTML = error;
        errorDiv.style.transition = 'opacity 0.5s';
        errorDiv.classList.add('visible');
        setTimeout(() => errorDiv.classList.remove('visible'), 2000);
    }    
}

export default new Modal();